/**
 * 组件公共类样式
 */
.z-css-text-omit {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.z-css-transition {
  transition: all 500ms ease; }

.z-css-common {
  -webkit-tap-highlight-color: transparent; }

.z-css-ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.z-css-text-left {
  text-align: left; }

.z-css-text-center {
  text-align: center; }

.z-css-text-right {
  text-align: right; }

.z-css-clear-fix:after {
  display: block;
  clear: both;
  content: ""; }

.z-css-float-left {
  float: left; }

.z-css-float-right {
  float: right; }

.z-css-vertical-middle {
  vertical-align: middle; }

.z-css-display-block {
  display: block; }

.z-css-display-inline-block {
  display: inline-block; }

.z-css-cursor-pointer {
  cursor: pointer; }

.z-css-hide {
  display: none !important; }

.z-css-invisible {
  visibility: hidden !important; }

.z-css-device-size {
  position: absolute;
  height: 0;
  width: 0;
  overflow: hidden;
  visibility: hidden;
  z-index: -999; }
  .z-css-device-size::after {
    content: "xl"; }
  @media only screen and (max-width: 1911px) {
    .z-css-device-size::after {
      content: "l"; } }
  @media only screen and (max-width: 991px) {
    .z-css-device-size::after {
      content: "m"; } }
  @media only screen and (max-width: 767px) {
    .z-css-device-size::after {
      content: "s"; } }
  @media only screen and (max-width: 575px) {
    .z-css-device-size::after {
      content: "xs"; } }

.z-css-motion-rip {
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden; }
  .z-css-motion-rip:after {
    content: "";
    position: absolute;
    background-color: rgba(255, 255, 255, 0.3);
    top: 50%;
    left: 50%;
    width: 80%;
    height: 200%;
    border-radius: 50%;
    -webkit-transform: translate(-50%, -50%) scaleX(1);
        -ms-transform: translate(-50%, -50%) scaleX(1);
            transform: translate(-50%, -50%) scaleX(1);
    -webkit-animation: z-css-motion-rip 2s infinite ease-in-out;
            animation: z-css-motion-rip 2s infinite ease-in-out; }

@-webkit-keyframes z-css-motion-rip {
  0% {
    -webkit-transform: translate(-50%, -50%) scaleX(1);
            transform: translate(-50%, -50%) scaleX(1); }
  50% {
    -webkit-transform: translate(-50%, -50%) scaleX(0.8);
            transform: translate(-50%, -50%) scaleX(0.8); }
  100% {
    -webkit-transform: translate(-50%, -50%) scaleX(1);
            transform: translate(-50%, -50%) scaleX(1); } }

@keyframes z-css-motion-rip {
  0% {
    -webkit-transform: translate(-50%, -50%) scaleX(1);
            transform: translate(-50%, -50%) scaleX(1); }
  50% {
    -webkit-transform: translate(-50%, -50%) scaleX(0.8);
            transform: translate(-50%, -50%) scaleX(0.8); }
  100% {
    -webkit-transform: translate(-50%, -50%) scaleX(1);
            transform: translate(-50%, -50%) scaleX(1); } }
